import Vue from "vue";
import Router from "vue-router";
import Vuex from 'vuex'
import ElementUI from 'element-ui';
import index from "@/components/TrangChu/index.vue";
 import DetailHotel from "@/components/DetailHotel/DetailHotel.vue";
import Category from "@/components/Category/Category.vue";
// import About from "@/components/About/About.vue";
// import Blog from "@/components/Blog/Blog.vue";
import ListPromotion from "@/components/Promotion/ListPromotion.vue";
import PromotionDetail from "@/components/Promotion/PromotionDetail.vue";
import Area from "@/components/Area/Area.vue";
import Search from "@/components/Search/Search.vue";
import Vision from "@/components/Vision/Vision.vue";
import About from "@/components/About/About.vue";
import Recruitment from "@/components/Recruitment/Recruitment.vue";
import Comment from "@/components/Comment/Comment.vue";
 import Error404 from "@/components/Error404/Error404.vue";
Vue.use(ElementUI)
Vue.use(Router);
Vue.use(Vuex)
export default new Router({
  routes: [
    {
      path: "/",
      name: "index",
      component: index
    },
    {
      path: "/detailhotel/:Sn",
      name: "DetailHotel",
      component: DetailHotel
    },
    
    {
      path: "/listPromotion",
      name: "ListPromotion",
      component: ListPromotion
    },
     {
      path: "/promotionDetail",
      name: "PromotionDetail",
      component: PromotionDetail
    },
    
     {
      path: "/about",
      name: "About",
      component: About
    },
    {
      path: "/Comment/:Sn",
      name: "Comment",
      component: Comment
    },
     {
      path: "/vision",
      name: "Vision",
      component: Vision
    },
    {
      path: "/search",
      name: "Search",
      component: Search
    },{
      path: "/recruitment",
      name: "Recruitment",
      component: Recruitment
    },
     {
      path: "/category/:Sn",
      name: "Category",
      component: Category
    },
    {
      path: '/error404',
      component: Error404,
      name: '',
      hidden: true
    },
    {
      path: '*',
      redirect: '/error404',
      hidden: true
    }
    
  ],
  scrollBehavior() {
        return {x: 0, y: 0}
    }
});



