<template>
<div class="_1aU_RW padding-top-2rem;background-color:#ffffff">
    <div class="_1VZIbu1">
        <div class="gallery">
            <div v-for="(dataHotel, index) in  this.$store.state.dataSlide" :key="index">
                <div style="padding:0 10px">
                    <el-image class=" border-radius res-ponsive " :src="'https://go2joylocal.s3-ap-southeast-1.amazonaws.com/'+dataHotel.imagePath">
                    </el-image>
                </div>
            </div>
        </div>
    </div>
</div>
</template>
<script>
import Slick from 'vue-slick';
import axios from 'axios';

export default {
    name: 'Slider',
    components: {
        Slick
    },
    data() {
        return {
            data: {
            },
            
        }
    },
    updated() {
        $('.gallery').slick({
            autoplay: true,
            dots: false,
            arrows: false,
            autoplaySpeed: 3000,
            loop:true,
            slidesToShow: 1,
            slidesToScroll: 1,
            infinite: true,
            fade: true,
            cssEase: 'linear'

        });
    }
}
</script>

<style scoped>
.slick-arrow {
    display: none !important;
}

.slick-dots {
    display: none !important;
}

#carousel-1 {
    border-radius: 30px !important;
}

#carousel-1 .carousel-item {
    border-radius: 30px !important;
}

.img-fluid {
    border-radius: 30px !important;
}

.padding-top-2rem {
    padding-top: 0;
}
@media only screen and (max-width: 374px) and (min-width: 320) {
    ._1VZIbu1 {
    padding: 10rem 0 2rem 0;
}
}
@media only screen and (max-width: 480px) and (min-width: 375) {
    .padding-top-2rem {
