    },
    data() {
        return {
            activeIndex: '1',
            activeIndex2: '1',
            languages: [{
                flag: 'us',
                language: 'en',
                title: 'English'
            }, {
                flag: 'es',
                language: 'vi',
                title: 'Tiếng Việt'
            }],
            isShowVi: false,
            isShowEn: false,
        }
    },
    created() {
        this.showLanguage();
    },
    methods: {

        changeLocale(locale) {
            i18n.locale = locale;
        },
        handleSelect(key, keyPath) {
            console.log(key, keyPath);
        },
        showLanguage(n) {
            // console.log('n: ', n)
            if (n == 'vi') {
                this.isShowEn = false,
                    this.isShowVi = false
                // console.log('isShowEn, isShowvi', this.isShowEn, this.isShowVi)
            } else if (n == 'en') {
                this.isShowEn = true,
                    this.isShowVi = true
                // console.log('isShowEn, isShowvi', this.isShowEn, this.isShowVi)
            } else if (n == '') {
                this.isShowVi = false
                this.isShowEn = false
            }
        },

    }

}
